  padding: 5px;
  font-size: 16px;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 8px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.button:hover {
  background-color: #0056b3;
}

.text-field {
  min-height: 100px;
